<script>
export default {
  name: "second-selection",
  data() {
    return {
      secondList: [
        {
          img: require(`@/assets/images/饮品&咖啡商品配图/咖啡商品/1.jpg`),
          content: 'Emerald Manor coffee bean gift box',
        },
        // {
        //   img: require(`@/assets/images/饮品&咖啡商品配图/咖啡商品/2.jpg`),
        //   content: 'Blue Mountain coffee bean gift box',
        // },
        {
          img: require(`@/assets/images/饮品&咖啡商品配图/咖啡商品/3.jpg`),
          content: 'Cat Poop Coffee Supreme gift box',
        },
        {
          img: require(`@/assets/images/饮品&咖啡商品配图/咖啡商品/4.jpg`),
          content: 'Special origin hanging ear coffee',
        },
        {
          img: require(`@/assets/images/饮品&咖啡商品配图/咖啡商品/5.jpg`),
          content: 'Characteristic flavor solid drink',
        },
        {
          img: require(`@/assets/images/饮品&咖啡商品配图/咖啡商品/6.jpg`),
          content: 'Specialty coffee bottled ready-to-drink',
        },
        {
          img: require(`@/assets/images/饮品&咖啡商品配图/咖啡商品/7.jpg`),
          content: 'Select sourced coffee beans'
        },
      ],
      colWidth: 0,
      containerWidth: 0,
      showCol: [],
      curTranslateX: 0,
      curLineLeft: 0
    }
  },
  computed: {
    disablePrev() {
      return this.curLineLeft === 0
    },
    disableNext() {
      console.log('啊啊啊',100 - 100 / (this.secondList.length - this.showCol.length + 1))
      return this.curLineLeft.toFixed(2) === (100 - 100 / (this.secondList.length - this.showCol.length + 1)).toFixed(2)
    }
  },
  methods: {
    move(num) {
      if (num < 0) {
        if (this.disableNext) return;
        this.curLineLeft += 100 / (this.secondList.length - this.showCol.length + 1);
        this.$refs.lightLine.style.left = `${this.curLineLeft}%`
      } else {
        if (this.disablePrev) return;
        this.curLineLeft -= 100 / (this.secondList.length - this.showCol.length + 1);
        this.$refs.lightLine.style.left = `${this.curLineLeft}%`
      }
      this.curTranslateX += num
      this.$refs.colBoxRef.$el.style.transform = `translateX(${this.curTranslateX}px)`;
      console.log('123',this.curLineLeft)
    }
  },
  mounted() {
    const colRef = this.$refs.colRef
    const containerRef = this.$refs.containerRef
    this.containerWidth = containerRef.getBoundingClientRect().right;
    this.colWidth = colRef[0].$el.offsetWidth;
    colRef.forEach(item => {
      const right = item.$el.getBoundingClientRect().right;
      if (right < this.containerWidth) {
        this.showCol.push(item)
      }
    })
    // 未显示的几个加一个
    this.$refs.lightLine.style.width = `${100 / (this.secondList.length - this.showCol.length + 1)}%`;
    this.$refs.lightLine.style.left = `${this.curLineLeft}%`
  }
}
</script>

<template>
  <div class="min-h-screen h-screen bg-grey" id="secondPage">
    <div class="container mx-auto relative border-b border-gray-400 h-full sm:px-20">
      <p class="text-coffee text-md mt-10">Recommended Coffee</p>
      <p class="text-3xl my-5 font-bold">Discover Unforgettable <br/> Coffee Flavors</p>
      <div class="w-screen  overflow-x-auto hidden-scrollbar" ref="containerRef">
        <el-row :gutter="20" class="flex w-full mt-10 transition-all" ref="colBoxRef">
          <el-col :span="5" v-for="(item,index) in secondList" :key="index"
                  class="h-96 flex-shrink-0 text-center overflow-hidden"
                  ref="colRef">
            <img class="w-full h-5/6" :src="item.img" alt="">
            <span class="font-bold text-lg">{{ item.content }}</span>
          </el-col>
        </el-row>
      </div>
      <div class="flex items-center mt-10">
        <div @click="move(colWidth)" :class="{'grayscale filter': disablePrev}"
             class="rounded-full border-coffeeLight border-2 z-50 cursor-pointer mx-3">
          <img class="w-10 h-10" src="@/assets/icon/toLeft-coffee.png" alt="">
        </div>
        <div class="flex-grow relative h-1">
          <!--          灰色-->
          <div class="w-full rounded-xl h-full bg-gray-500"></div>
          <!--          高亮-->
          <div class="bg-coffeeLight absolute -top-1/4 rounded-xl h-1.5 transition-all" ref="lightLine"></div>
        </div>
        <div @click="move(-colWidth)" :class="{'grayscale filter': disableNext}"
             class="rounded-full border-coffeeLight border-2 z-50 cursor-pointer mx-3">
          <img class="w-10 h-10" src="@/assets/icon/toRight-coffee.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">

</style>